@import url('https://fonts.googleapis.com/css?family=Encode+Sans+Expanded:200,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Sacramento&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-size: 62.5%;
    color: #333;
}

nav {
    width: 100%;
    height: 10vh;
    background-color: rgba(255, 255, 255, 0.18);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    position: relative;
    float: left;
    z-index: 99;
}

nav::before { 
    width: 100%; height: 100%;
    content: '';
    position: absolute;
    top: 0; left: 0;
    background-color: rgba(255, 255, 255, 0.18);
    -webkit-filter: blur(5px);
}

.nav-list {
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
    @media only screen and (max-width: 768px) {
        width: 100%;
    }
    @media only screen and (max-width: 480px) {
        font-size: 0.7rem;
    }
}

nav > ul > li {
    width: 100%;
    text-align: center;
}

.nav-links {
    width: 100%;
    font-family: 'Encode Sans Expanded', sans-serif;
    position: relative;
    text-decoration: none;
    color: #333;
    font-size: 1.3rem;
    font-weight: 200;
    z-index: 101;
    @media only screen and (max-width: 480px) {
        font-size: 0.8rem;
    }
    &:hover{
        font-weight: 700;
    }
}

.home,
.about,
.contact {
    height: 100vh;
    position: absolute;
    width: 100%;
    overflow: hidden;
}

.about {
    height: auto;
}

@media only screen and (max-width: 768px) {
    .about {
        height: auto;
    }
}

.home-content,
.about-content,
.contact-content {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    color: #fff;
    h1 {
        font-family: 'Sacramento', cursive;
        font-size : 5rem;
        margin-bottom: 0rem;
    }
    p {
        font-size: 1rem;
        letter-spacing: 1px;
    }
}

.about-content{
    width: 100%;
    height: 90%;
}

.contact-content {
    color: #333;
}

.contact-presentation {
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    background-color: #fff;
    border-radius: 5px;
    padding: 1rem;
    label {
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 1px;
        margin-bottom: 0.4rem;
    }
    input {
        width: 100%;
        border: 1px solid #555;
        border-radius: 5px;
        padding: 0.3rem;
        margin-bottom: 1rem;
        height: 2.5rem;
    }

    h1 {
        width: 100%;
        text-align: center;
        font-size: 3rem;
        margin-bottom: 0.5rem;
        letter-spacing: 2px;
    }

    p {
        font-size: 1rem;
        margin-bottom: 1rem;
    }
    
    /* Set a style for the submit button */
    .submitbtn {
        background-color: #8fd3f4;
        color: white;
        padding: 16px 20px;
        margin: 8px 0;
        font-size: 1.1rem;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        width: 100%;
        opacity: 0.9;
    }
    
    .submitbtn:hover {
        opacity: 1;
    }
    
    /* Add a blue text color to links */
    a {
        color: dodgerblue;
    }
}

.home-presentation {
    clip-path: polygon(6% 6%, 100% 15%, 83% 100%, 0 71%);
    padding: 7rem;
    background-color: #fbc2eb; 
}

.about-presentation {
    height: 100%;
    width: 100%;
    padding-top: 12vh;
    @media only screen and (max-width: 768px) {
        flex-direction: column;  
        padding-top: 7rem;         
    }
    .slide {
        float: left;
        width: 30%;
        margin: 0 1rem;
        height: auto;
        .inner{
            width: 30rem;
            height: 30rem;
            position: relative;
            top: 50%; left: 50%;
            transform: translate(-50%, -80%);
            @media only screen and (max-width: 768px) {
                margin-bottom: -25rem;
            }
            img { 
                margin: auto;
                height: 50%;
            }
            p {
                margin: auto;
                width: 80%;
                @media only screen and (max-width: 480px) {
                    width: 70%;
                }
            }  
        }
    }
}

.bg1{
    width: 30rem;
    height: 30rem;
    background-color: #4dc479;
    clip-path: polygon(20% 0%, 94% 21%, 92% 100%, 0 80%);
}
.bg2{
    width: 30rem;
    height: 30rem;
    background-color: #8a6bd1;
    clip-path: polygon(0 21%, 88% 0, 100% 53%, 46% 100%);
}
.bg3{
    width: 30rem;
    height: 30rem;
    background-color: #e44c51;
    clip-path: polygon(0 34%, 100% 0, 68% 100%, 46% 100%);
}

.home {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
    background-image: linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%);
}

.contact{
    background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
}

hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
}

